import React, { Component } from "react";
import { Grid, Row, Col, Image } from "react-bootstrap";

import { Card } from "../../components/Card/Card";
import { FormInputs } from "../../components/FormInputs/FormInputs";
import Button from "../../components/CustomButton/CustomButton";
import Apimanager from "../../NodeFunctions/Functions";
import SweetAlert from "react-bootstrap-sweetalert";
import config from "../../config";

class UserProfile extends Component {
  state = {
    cat_id: "",
    cat_name: "",
    picture_pathway: "",
    alert: null,
    selectedFile: null,
    imgSrc: "",
  };
  getInitialState = () => {
    return { file: [] };
  };

  _onChange = () => {
    // Assuming only image
    var file = this.refs.file.files[0];
    var reader = new FileReader();
    var url = file && reader.readAsDataURL(file);

    reader.onloadend = function (e) {
      this.setState({
        imgSrc: [reader.result],
        selectedFile: file,
      });
    }.bind(this);
  };
  updateform = (val) => {
    var warning = "";
    if (this.state.cat_name == "") {
      warning = "Please Enter Name";
    } else {
      warning = "";
    }
    if (warning) {
      this.setState({
        alert: (
          <SweetAlert
            warning
            style={{ display: "block", marginTop: "100px" }}
            title={warning}
            onConfirm={() => this.setState({ alert: null })}
            onCancel={() => this.setState({ alert: null })}
            confirmBtnBsStyle="warning"
          ></SweetAlert>
        ),
      });
      return;
    }
    var Formdata = new FormData();
    Formdata.append("image", this.state.selectedFile);
    if (this.state.selectedFile) {
      new Apimanager()
        .postrouteFormdata(
          `Admin/item_categories/upload/categories/${this.state.cat_name}/${this.state.cat_id}`,
          Formdata
        )
        .then((res) => {
          if (res.status === 200) {
            new Apimanager()
              .PutrouteByid(`Admin/item_categories/${this.state.cat_id}`, {
                name: this.state.cat_name,
              })
              .then((res) => {
                if (res.status == 200) {
                  this.setState({
                    alert: (
                      <SweetAlert
                        success
                        style={{ display: "block", marginTop: "100px" }}
                        title="Data Updated!"
                        onConfirm={() => this.setState({ alert: null })}
                        onCancel={() => this.setState({ alert: null })}
                        confirmBtnBsStyle="info"
                      ></SweetAlert>
                    ),
                  });
                }
              })
              .catch((e) => console.log(e));
          }
        })
        .catch((e) => console.log(e));
    } else {
      new Apimanager()
        .PutrouteByid(`Admin/item_categories/${this.state.cat_id}`, this.state)
        .then((res) => {
          // console.log(res)
          if (res.status == 200) {
            this.setState({
              alert: (
                <SweetAlert
                  success
                  style={{ display: "block", marginTop: "100px" }}
                  title="Data Updated!"
                  onConfirm={() => this.setState({ alert: null })}
                  onCancel={() => this.setState({ alert: null })}
                  confirmBtnBsStyle="info"
                ></SweetAlert>
              ),
            });
          }
        })
        .catch((e) => console.log(e));
    }
  };
  submitform = () => {
    var warning = "";
    if (this.state.cat_name == "") {
      warning = "Please Enter Name";
    } else {
      warning = "";
    }
    if (warning) {
      this.setState({
        alert: (
          <SweetAlert
            warning
            style={{ display: "block", marginTop: "100px" }}
            title={warning}
            onConfirm={() => this.setState({ alert: null })}
            onCancel={() => this.setState({ alert: null })}
            confirmBtnBsStyle="warning"
          ></SweetAlert>
        ),
      });
      return;
    }
    if (!this.state.selectedFile || this.state.selectedFile == null) {
      this.setState({
        alert: (
          <SweetAlert
            warning
            style={{ display: "block", marginTop: "100px" }}
            title="Please Select An Image !"
            onConfirm={() => this.setState({ alert: null })}
            onCancel={() => this.setState({ alert: null })}
            confirmBtnBsStyle="warning"
          ></SweetAlert>
        ),
      });
      return;
    }
    var Formdata = new FormData();
    Formdata.append("image", this.state.selectedFile);

    new Apimanager()
      .postroute("Admin/item_categories", { cat_name: this.state.cat_name })

      .then((res) => {
        if (res.status == 200) {
          // console.log(res.data,"cat")
          this.setState({
            ...res.data.category,
          });
          // console.log(res.data+"id")
        } else {
          return this.setState({
            alert: (
              <SweetAlert
                danger
                style={{ display: "block", marginTop: "100px" }}
                title="Can't Create Category"
                onConfirm={() => this.setState({ alert: null })}
                onCancel={() => this.setState({ alert: null })}
                confirmBtnBsStyle="danger"
              ></SweetAlert>
            ),
          });
        }
      })
      .then(() => {
        new Apimanager()
          .postrouteFormdata(
            `Admin/item_categories/upload/categories/${this.state.cat_name}/${this.state.cat_id}`,
            Formdata
          )
          .then((res) => {
            if (res.status == 200) {
              this.setState({
                alert: (
                  <SweetAlert
                    success
                    style={{ display: "block", marginTop: "100px" }}
                    title="Category Created!"
                    onConfirm={() => this.setState({ alert: null })}
                    onCancel={() => this.setState({ alert: null })}
                    confirmBtnBsStyle="info"
                  ></SweetAlert>
                ),
              });
            } else {
              this.setState({
                alert: (
                  <SweetAlert
                    warning
                    style={{ display: "block", marginTop: "100px" }}
                    title="Category Created But Error In Image Uploading !"
                    onConfirm={() => this.setState({ alert: null })}
                    onCancel={() => this.setState({ alert: null })}
                    confirmBtnBsStyle="warning"
                  ></SweetAlert>
                ),
              });
            }
          })
          .catch((e) => console.log(e));
      })
      .catch((e) => console.log(e));
  };
  componentDidMount = () => {
    if (this.props.location.data) {
      const { data } = this.props.location;
      this.setState({
        ...data,
      });
    }
  };
  render() {
    return (
      <div className="content">
        {/* {console.log(JSON.stringify(this.state.selectedFile))} */}
        <Grid fluid>
          <Row>
            <Col md={12}>
              <Card
                title="Add Categories"
                content={
                  <form>
                    <Row>
                      <Col md={5}></Col>
                      <Col xs={6} md={4}>
                        <Image
                          src={
                            this.state.imgSrc
                              ? this.state.imgSrc
                              : this.state.picture_pathway
                              ? `${config.url}public/images/${this.state.picture_pathway}`
                              : "data:image/jpeg;base64,"
                          }
                          roundedCircle
                          style={{ width: 200, height: 150, borderWidth: 2 }}
                        />
                      </Col>
                    </Row>
                    <FormInputs
                      ncols={["col-md-6", "col-md-6"]}
                      properties={[
                        {
                          label: "Category Id",
                          type: "text",
                          bsClass: "form-control",
                          placeholder: "Category Id",
                          defaultValue:
                            this.props.match.params.data == ":data"
                              ? ""
                              : this.state.cat_id,
                          disabled:
                            this.props.match.params.data == ":data"
                              ? false
                              : true,
                          onChange: (evt) => {
                            this.setState({ cat_id: evt.target.value });
                          },
                        },
                        {
                          label: "Category Name",
                          type: "text",
                          defaultValue:
                            this.props.match.params.data == ":data"
                              ? ""
                              : this.state.cat_name,
                          bsClass: "form-control",
                          placeholder: "Category Name",
                          onChange: (evt) => {
                            this.setState({ cat_name: evt.target.value });
                          },
                        },
                      ]}
                    />
                    <Row>
                      <input
                        ref="file"
                        type="file"
                        name="user[image]"
                        multiple="true"
                        onChange={this._onChange}
                      />
                      <Col md={12}>
                        {!this.state.cat_id ? (
                          <Button
                            onClick={() => this.submitform()}
                            bsStyle="warning"
                            style={{
                              marginLeft: "45%",
                              height: 40,
                              width: 120,
                            }}
                          >
                            Save
                          </Button>
                        ) : (
                          <Button
                            onClick={() => this.updateform()}
                            bsStyle="warning"
                            style={{
                              marginLeft: "45%",
                              height: 40,
                              width: 120,
                            }}
                          >
                            Update
                          </Button>
                        )}
                      </Col>
                    </Row>
                  </form>
                }
              />
            </Col>
          </Row>
        </Grid>
        {this.state.alert}
      </div>
    );
  }
}

export default UserProfile;
